<template>  
  <header class="fixed z-40 flex flex-col items-center justify-between w-full bg-sky-800 mb-5">
    
    <div class="flex items-center justify-between w-full bg-sky-800 px-5 py-2">
      <div class="flex items-center gap-2 md:gap-x-10">
        <!-- Logo -->
        <router-link :to="{name: 'Home'}" class="w-2/12 m-0 text-white">
          <figure class="">
            <img :src="logo_img" alt="logo">
          </figure>
        </router-link>
        
        <!-- Search Desktop -->
        <div class="hidden bg-sky-800 mb-0 md:flex w-9/12 md:w-full items-center gap-0 h-auto">
          <select v-model="searchStore.type"  class="w-28 h-10 bg-white border-2 border-amber-400 px-3 py-1 rounded-l-lg">
            <option value="all" class="">Todos</option>
            <option value="category" class="">Categorías</option>
            <option value="products" class="">Productos</option>
            <option value="prices" class="">Precios</option>
          </select>
          <input 
            type="search"          
            id="search" 
            class="flex h-10 px-3 py-1 w-full placeholder-gray-400 text-gray-800 border-2 border-amber-400 focus:outline-none focus:border-amber-400" 
            placeholder="Buscador"
            v-model="searchStore.search"
            @keyup.enter="searchStore.goToSearchView()"
          >
          <button class="flex items-center px-3 py-2 h-10 bg-amber-400 rounded-r-lg" @click="searchStore.goToSearchView()"><i class="fa fa-search text-amber-800"></i></button>
        </div>

        <!-- Lang -->
        <div class="">
    
        </div>
      </div>


      <!-- Iconos -->
      <div class="flex items-center gap-5">
        
        <!-- User -->
        <router-link :to="{name: 'Dashboard'}" class="w-5"><i class="fa fa-user text-white fa-lg"></i></router-link>
        
        <!-- Cart -->
        <router-link :to="{name: 'ShoppingCart'}" class="w-5">
            <i class="fa fa-shopping-cart fa-lg text-white relative"><span class="absolute -top-1 left-[10px] text-sky-800 text-center text-[8px]">{{ cart.totalProducts }}</span></i>
        </router-link>
        
        <!-- Menu -->
        <div class="relative w-5">
          <i class="fa fa-bars text-white  fa-lg" v-show="!openMenu"  @click="openMenu = !openMenu"></i>
          <i class="fa fa-times text-white fa-lg" v-show="openMenu"   @click="openMenu = !openMenu"></i>
        </div>

        <!-- Navigation -->
        <nav class="absolute top-14 right-0 transition-opacity delay-100 bg-slate-800/70 text-white" v-show="openMenu">
          <ul class="w-full py-5">            
            <!-- <li>p2</li>
            <li>p3</li>
            <li>p4</li> -->
            <li v-if="auth.is_login"><button @click="closeSession">Cerrar Sesión</button></li>
          </ul>
        </nav>
      </div>
    </div>

    <!-- Search Mobile -->
    <div class="md:hidden flex w-full bg-sky-800 p-2 items-center gap-0 h-auto">
      <select v-model="searchStore.type" class="w-28 h-10 bg-white border-2 border-amber-400 px-3 py-1 rounded-l-lg">
        <option value="all" class="">Todos</option>
        <option value="category" class="">Categorías</option>
        <option value="products" class="">Productos</option>
        <option value="prices" class="">Precios</option>
      </select>
      <input 
        type="search"          
        id="search" 
        class="flex h-10 px-3 py-1 w-full placeholder-gray-400 text-gray-800 border-2 border-amber-400 focus:outline-none focus:border-amber-400" 
        placeholder="Buscador"
        v-model="searchStore.search"
        @keyup.enter="searchStore.goToSearchView()"
      >
      <button class="flex items-center px-3 py-2 h-10 bg-amber-400 rounded-r-lg" @click="searchStore.goToSearchView()"><i class="fa fa-search text-amber-800"></i></button>
    </div>

  </header>
</template>

<script setup>
import CONFIG from '../../../config.js'
import { ref } from "vue";
import {useAuthStore} from "@/store/authStore"
import {useCartStore} from "@/store/cartStore"
import {useSearchStore} from "@/store/searchStore"
import router from '@/router';

const auth = useAuthStore();
const cart = useCartStore();
const searchStore = useSearchStore();
const logo_img = ref(CONFIG.GALLERY + "general/logo/logo-white.png")
const openMenu = ref(false)

const closeSession = () => {
  let logout = auth.logout();
  if(logout)
  router.push({ name: 'Login'})
}

</script>
<style scoped>

footer .social-media p {
  font-size: 14px;
}

.input_search{
  @apply flex h-full px-3 py-2 border;
}

li{
  @apply hover:bg-slate-700/70 w-full px-5;
}
</style>
